.hero {

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;

    &-flex-end {
      align-items: flex-end;

      @include mobile {
        align-items: flex-start;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    padding: 0;
    font-weight: 400;
    font-size: 40px;
    line-height: 100%;
    color: var(--dark-color);

    @include mobile {
      font-size: 28px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 27px;
    padding: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 100%;
    color: var(--dark-color);

    @include mobile {
      font-size: 18px;
      margin-bottom: 22px;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    @include small-tablet {
      opacity: 0.3;
    }
  }

  &__slider {
    position: relative;
    height: 592px;

    @include small-tablet {
      height: 450px;
    }
  }
}

.swiper-pagination {
  bottom: 20px !important;
}

.swiper-pagination-bullet {
  width: 8px !important;
  height: 8px !important;
  background-color: var(--light-color) !important;
  opacity: 1 !important;
}

.swiper-pagination-bullet-active {
  border: 1px solid #7D7D7D !important;
}
