.news {
  padding-top: 85px;
  padding-bottom: 85px;
  background-color: var(--blue-color);

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .all__news {
    display: inline-flex;
    margin-bottom: 45px;
    font-weight: 400;
    font-size: 14px;
    line-height: 15px;
    color: var(--grey-color);

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  .swiper-wrapper {
    margin-bottom: 30px;
  }

  &__item {
    display: flex;
    flex-direction: column;

    min-height: 292px;
    border-radius: 10px;
    background-color: var(--light-color);

    &-img {
      width: 100%;
      height: 100%;

      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
      }
    }

    &-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 18px 24px 20px 24px;
    }

    &-title {
      margin: 0;
      padding: 0;
      margin-bottom: 12px;
      font-family: 'GothicA1', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: var(--dark-color);
    }

    &-descr {
      font-family: 'GothicA1', sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: var(--dark-color);
      margin-bottom: 20px;
    }

    &-arrow {
      svg path {
        fill: var(--brown-color);
        transition: fill 0.3s ease-in;
      }
    }
  }

  .slider__btns {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-left: auto;
  }

  .news__slider-btn {
    @include bg-cover;

    width: 31px;
    height: 8px;

    &:not(:last-child) {
      margin-right: 5px;
    }

    &--prev {
      background-image: url("../img/icon/prev.svg");
    }

    &--next {
      background-image: url("../img/icon/next.svg");
    }
  }
}
