.services {
  padding-top: 110px;
  padding-bottom: 100px;

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__title {
    margin-bottom: 25px;
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 38px;

    @media (max-width: 992px) {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 25px;
      row-gap: 25px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 20px;
    }
  }

  &__item {
    display: flex;
    border-radius: 8px;
    background-color: var(--lightPrimary-color);
    transition: background-color 0.3s ease-in;

    &-content {
      padding: 40px 10px 20px 20px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &-img {
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
      width: 140px;
      height: 200px;

      @include small-tablet {
        width: 120px;
        height: 150px;
      }
    }

    &-title {
      margin: 0;
      padding: 0;

      &--link {
        font-weight: 400;
        font-size: 28px;
        line-height: 110%;
        color: var(--brown-color);
        transition: color 0.3s ease-in;

        @include small-tablet {
          font-size: 24px;
        }

        @include mobile {
          font-size: 20px;
        }
      }
    }

    &-arrow {
      svg path {
        fill: var(--brown-color);
        transition: fill 0.3s ease-in;
      }
    }
  }
}

.services__item:hover {
  background-color: #E8DFC5;
}

.services__item:hover .services__item-title--link {
  color: var(--dark-color);
}

.services__item:hover .services__item-arrow svg path {
  fill: var(--dark-color);
}
