.header {
  &__content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px 0;

    &::before {
      position: absolute;
      content: "";
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
    }
  }

  .logo {
    display: flex;
    align-items: center;
    color: var(--dark-color);

    &__img {
      margin-right: 10px;
      width: 74px;
      height: 74px;
    }

    &__descr {
      margin: 0;
      padding: 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 100%;
      color: var(--dark-color);
    }
  }

  &__location {
    display: flex;
    align-items: start;
    margin-bottom: 22px;

    &-img {
      margin-right: 5px;
    }

    &-text {
      font-weight: 400;
      font-size: 15px;
      line-height: 17px;
    }
  }

  &__contacts {
    display: flex;
    align-items: start;

    &-img {
      margin-right: 5px;
    }

    &-content {
      display: flex;
      flex-direction: column;
      line-height: 110%;
      letter-spacing: 0.05em;
    }

    &-phone {
      font-size: 20px;
      text-decoration: none;
      color: var(--dark-color);
      transition: text-decoration 0.3s ease-in, color 0.3s ease-in;

      &:hover {
        text-decoration: underline;
        color: var(--secondary-color);
      }
    }

    &-email {
      font-size: 16px;
      text-decoration: none;
      color: var(--dark-color);
      transition: text-decoration 0.3s ease-in, color 0.3s ease-in;

      &:hover {
        text-decoration: underline;
        color: var(--secondary-color);
      }
    }
  }

  &__schedule {
    display: flex;
    align-items: start;

    &-img {
      margin-right: 7px;
    }

    &-text {
      font-size: 18px;
      line-height: 125%;
      margin-bottom: 5px;
    }
  }

  &__col3 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .nav {
    display: flex;
    justify-content: right;
    padding: 16px 0 17px 0;

    &__list {
      display: flex;
      align-items: center;
    }

    &__item:not(:last-child) {
      margin-right: 52px;
    }

    &__link {
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      color: var(--dark-color);
      text-decoration: none;
      transition: text-decoration 0.3s ease-in;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.social {
  display: flex;
  align-items: center;

  &__item {
    opacity: 1;
    transition: opacity 0.3s ease-in;

    &:hover {
      opacity: 0.7;
    }
  }

  &__item:not(:last-child) {
    margin-right: 10px;
  }
}
