.price {
  &__heading {
    margin-bottom: 45px;

    @include small-tablet {
      margin-bottom: 35px;
    }
  }

  &__title {
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 60px;
    line-height: 110%;
    color: var(--black-color);

    @include small-tablet {
      font-size: 40px;
    }
  }

  &__text {
    margin: 0;
    padding: 0;
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 15px;
    color: var(--grey-color);
  }

  &-breadcrumb {
    margin-top: 0;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 100px;

      @include small-tablet {
        margin-bottom: 70px;
      }

      @include mobile {
        margin-bottom: 40px;
      }
    }

    &-title__decor {
      display: flex;
      align-items: center;

      &-icon {
        min-width: 25px;
        min-height: 40px;
        margin-right: 10px;
        margin-left: 10px;
      }
    }

    &-title {
      margin: 0;
      padding: 0;
      margin-bottom: 8px;
      font-family: "Forum", sans-serif;
      text-align: center;
      font-weight: 400;
      font-size: 40px;
      line-height: 95.5%;

      @include small-tablet {
        font-size: 28px;
      }

      @include mobile {
        font-size: 22px;
      }
    }
  }

  &__list {
    &-item {
      display: flex;
      align-items: start;
      justify-content: space-between;
      font-family: "Forum", sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 95.5%;
      color: var(--black-color);

      @include small-tablet {
        font-size: 20px;
      }

      @include mobile {
        font-size: 17px;
      }

      &:not(:last-child) {
        margin-bottom: 25px;

        @include mobile {
          margin-bottom: 15px;
        }
      }
    }

    &-name {
      text-align: left;
    }

    &-price {
      text-align: right;
      margin-left: 25px;

      @include mobile {
        margin-left: 15px;
      }
    }
  }

  &__mark {
    margin-top: 40px;
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 110%;
    color: var(--grey-color);

    @include small-tablet {
      margin-top: 30px;
      font-size: 18px;
    }

    @include mobile {
      margin-top: 20px;
      font-size: 16px;
    }
  }
}

.left-line {
  width: 50%;
  height: 1px;
  background-color: #DBC88D;
}

.right-line {
  width: 50%;
  height: 1px;
  background-color: #DBC88D;
}

.center-line {
  margin: 0;
  margin-bottom: 35px;
  width: 100%;
  height: 1px;
  background-color: #DBC88D;

  @include mobile {
    margin-bottom: 20px;
  }
}
