.why-us {
  padding-top: 85px;
  padding-bottom: 85px;

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 50px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__heading {
    display: flex;
    align-items: center;
    margin-bottom: 60px;

    @include small-tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__decor {
    margin-right: 15px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 65px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 25px;
      row-gap: 45px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 35px;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    &-img {
      margin-right: 24px;

      min-width: 40px;
      min-height: 40px;

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-text {
      font-weight: 400;
      font-size: 20px;
      line-height: 100%;
      color: var(--brown-color);
    }
  }
}
