.contacts {
  padding-top: 85px;
  padding-bottom: 50px;

  @include small-tablet {
    padding-top: 50px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 30px;
  }

  &__container {
    display: flex;
    align-items: flex-end;

    @media (max-width: 900px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__title {
    margin-bottom: 55px;

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  &__col {
    width: 50%;

    @media (max-width: 900px) {
      width: 100%;
      margin-bottom: 40px;
    }

    @media (max-width: 390px) {
      margin-bottom: 25px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 70px;

    @media (max-width: 900px) {
      margin-bottom: 30px;
    }

    @media (max-width: 390px) {
      margin-bottom: 15px;
    }
  }

  &__item {
    width: 100%;

    &:not(:last-child) {
      width: 50%;

      @media (max-width: 390px) {
        width: 100%;
      }
    }

    &:nth-child(2) {
      @media (max-width: 390px) {
        margin-top: 20px;
      }
    }

    &:nth-child(3) {
      margin-top: 40px;

      @media (max-width: 900px) {
        margin-top: 20px;
      }
    }

    &-title {
      margin: 0;
      padding: 0;
      margin-bottom: 22px;
      font-weight: 400;
      font-size: 24px;
      line-height: 26px;
      letter-spacing: 0.015em;
      color: var(--dark-color);

      @include small-tablet {
        font-size: 20px;
        margin-bottom: 15px;
      }

      @media (max-width: 390px) {
        margin-bottom: 5px;
      }

      &-address {
        margin-bottom: 5px;

        @media (max-width: 390px) {
          margin-bottom: 0;
        }
      }
    }

    &-text {
      font-weight: 400;
      font-size: 15px;
      line-height: 17px;
      margin-bottom: 20px;

      @media (max-width: 390px) {
        margin-bottom: 10px;
      }
    }

    &-column {
      display: flex;
      flex-direction: column;
    }

    &-phone {
      font-size: 20px;
      text-decoration: none;
      color: var(--dark-color);
      transition: text-decoration 0.3s ease-in, color 0.3s ease-in;

      &:hover {
        text-decoration: underline;
        color: var(--secondary-color);
      }
    }

    &-email {
      font-size: 16px;
      text-decoration: none;
      color: var(--dark-color);
      transition: text-decoration 0.3s ease-in, color 0.3s ease-in;

      &:hover {
        text-decoration: underline;
        color: var(--secondary-color);
      }
    }

    &-schedule {
      display: flex;
      align-items: start;
      font-weight: 400;
      font-size: 18px;
      line-height: 100%;
      letter-spacing: 0.015em;
      color: #7C7C7C;

      &-day {
        text-align: right;
        margin-right: 32px;
      }
    }
  }
}

.map {
  margin-right: -100px;
  width: 585px;
  height: 440px;

  @media (max-width: 900px) {
    margin: 0;
    width: 100%;
    height: 400px;
  }

  @include mobile {
    height: 230px;
  }
}
