.team {
  padding-top: 85px;
  padding-bottom: 85px;

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__heading {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;

    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 50px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 25px;
      row-gap: 40px;
    }

    @media (max-width: 450px) {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 30px;
    }
  }

  &__item {
    text-align: center;

    &-img {
      margin-bottom: 25px;

      @include small-tablet {
        margin-bottom: 15px;
      }

      img {
        border-radius: 30px;
      }
    }

    &-name {
      margin-bottom: 10px;
      font-size: 24px;
      line-height: 120%;

      @include small-tablet {
        font-size: 20px;
        margin-bottom: 5px;
      }
    }

    &-special {
      font-size: 18px;
      line-height: 100%;

      @include small-tablet {
        font-size: 16px;
      }
    }
  }
}
