.specialist {
  &__main {
    margin-bottom: 30px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 32px;

    @media (max-width: 992px) {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 30px;
    }

    &-content {
      margin-top: 40px;

      @media (max-width: 992px) {
        margin-top: 10px;
      }
    }

    &-name {
      margin: 0;
      padding: 0;
      margin-bottom: 30px;
      font-family: "Forum", sans-serif;
      font-weight: 400;
      font-size: 60px;
      line-height: 110%;

      @media (max-width: 992px) {
        margin-bottom: 20px;
        font-size: 40px;
      }

      @include mobile {
        margin-bottom: 10px;
        font-size: 30px;
      }
    }

    &-special {
      margin: 0;
      padding: 0;
      font-family: "Forum", sans-serif;
      font-weight: 400;
      font-size: 28px;
      line-height: 110%;
      color: var(--grey-color);

      @media (max-width: 992px) {
        font-size: 24px;
      }

      @include mobile {
        font-size: 20px;
      }
    }

    &-img {
      img {
        border-radius: 8px;
      }
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 32px;
    margin-bottom: 80px;

    @media (max-width: 992px) {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 30px;
      margin-bottom: 40px;
    }
  }

  &__title {
    margin: 0;
    padding: 0;
    margin-bottom: 25px;
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 28px;
    line-height: 100%;

    @include mobile {
      font-size: 24px;
      margin-bottom: 15px;
    }
  }

  &__text {
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 100%;

    &:not(:last-child) {
      margin-bottom: 80px;

      @media (max-width: 992px) {
        margin-bottom: 30px;
      }
    }
  }

  &__certificates {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 32px;

    @include mobile {
      column-gap: 20px;
      row-gap: 20px;
    }
  }

  &__portfolio {

    &-slider {
      margin-bottom: 80px;

      @media (max-width: 992px) {
        margin-bottom: 60px;
      }

      @include mobile {
        margin-bottom: 30px;
      }

      .swiper-wrapper {
        margin-bottom: 50px;

        @include small-tablet {
          margin-bottom: 30px;
        }
      }
    }

    &-item {
      position: relative;
    }

    &-text {
      position: absolute;
      content: "";
      left: 12px;
      bottom: 7px;
      font-family: "Forum", sans-serif;
      font-weight: 400;
      font-size: 13px;
      line-height: 14px;
      color: var(--light-color);
    }

    &-img {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
      }
    }
  }
}

.slider__btns {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-left: auto;
}

.specialist-portfolio__slider-btn {
  @include bg-cover;

  width: 31px;
  height: 8px;

  &:not(:last-child) {
    margin-right: 5px;
  }

  &--prev {
    background-image: url("../img/icon/prev.svg");
  }

  &--next {
    background-image: url("../img/icon/next.svg");
  }
}
