.team {

  &__row {
    padding-top: 100px;
    padding-bottom: 100px;
    row-gap: 32px;
  }

  &__specialist-img {
    img {
      width: 100%;
      height: 100%;
      border-radius: 8px 8px 0px 0px;
      object-fit: cover;
    }
  }

  &__specialist-info {
    text-align: center;
    padding: 12px 10px 14px 10px;
    background-color: #f7f7f7;
  }

  &__specialist-name {
    margin: 0;
    padding: 0;
    margin-bottom: 14px;
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 22px;
    line-height: 24px;

    &--link {
      color: var(--black-color);
      transition: color 0.3s ease-in;

      &:hover {
        color: var(--primary-color);
      }
    }
  }

  &__specialist-special {
    margin: 0;
    padding: 0;
    font-family: "Forum", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 90%;
    color: var(--grey-color);
  }
}

